.option-button {
  padding: 7px 0px;
}

.option-button .button__image {
  width: 70%;
  margin: auto;
  background: white;
  padding: 15px;
  border-radius: 50%;
  border: 2px solid #e8e7e7;
  box-shadow: none;
  border-width: 1px;
  cursor: pointer;
}

.option-button .button__image img {
  width: 100%;
}

.option-button .button__image:hover {
  background: rgba(0, 120, 128, 0.3);
}

.option-button.create-button {
  position: relative;
  z-index: 2;
}

.option-button.create-button .button__image {
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
}

.option-button.create-button .button__image:hover {
  background: white;
}

.option-button.create-button .popover-line {
  position: absolute;
  left: 90%;
  z-index: 1;
  background: white;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
  border: 1px solid #e8e7e7;
  top: 50%;
  width: 100px;
  padding: 10px;
  transform: translateY(-60%);
  border-radius: 7px;
}

.option-button.create-button .popover-line h5 {
  font-size: 14px;
}

.option-button.create-button .create-button-form {
  position: absolute;
  opacity: 0;
  left: 90%;
  top: 20px;
  transform: translateY(100px);
  transition: 0.3s;
  width: 350px;
  pointer-events: none;
}

.option-button.create-button .create-button-form.active {
  transform: translate(0);
  pointer-events: all;
  opacity: 1;
  background: white;
  z-index: 2;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
  border: 2px solid #e8e7e7;
  border-radius: 5px;
}

.option-button.create-button .create-button-form.active .form__wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 5px 10px;
}

.option-button.create-button .create-button-form.active .form__wrapper .form__input {
  border-width: 0 0 1px 0;
  font-style: italic;
  font-size: 14px;
}

.image-uploader-thumb {
  width: 100%;
  border-radius: 5px;
}

.image-uploader-thumb img {
  width: 100%;
  margin: auto;
  display: block;
}

.addPhotoButton {
  position: relative;
  z-index: 1;
}

.addPhotoButton:hover .select_grid_form {
  transform: translate(0);
  opacity: 1;
  pointer-events: all;
}

.addPhotoButton .select_grid_form {
  transform: translateY(100px);
  transition: 0.3s;
  opacity: 0;
  pointer-events: none;
}

.addPhotoButton .select_grid_form.active {
  transform: translate(0);
  opacity: 1;
  pointer-events: all;
}

.select_grid_form {
  position: absolute;
  left: 90%;
  width: 250px;
  background: white;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
  padding: 10px;
  border: 1px solid #e8e7e7;
  top: 20px;
  z-index: 2;
}

.select_grid_form .one_to_two {
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin: auto;
}

.select_grid_form .one_to_two .digit {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  border-radius: 999px;
  border: 1px solid #e8e7e7;
  font-size: 14px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: relative;
}

.select_grid_form .one_to_two .digit.one {
  position: relative;
}

.select_grid_form .one_to_two .digit.one::after {
  position: absolute;
  content: "";
  display: block;
  width: 120px;
  height: 5px;
  background: rgba(0, 120, 128, 0.2);
  left: 100%;
}

.select_grid_form .one_to_two .digit.active {
  color: #007880;
  border-color: #007880;
}

.select_grid_form .select_grid__image {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.select_grid_form .select_grid__image img {
  width: 100px;
  cursor: pointer;
}

.select_grid_form .point_2_upload_image .image-uploader-thumb {
  border: none;
  padding: 10px;
}

.select_grid_form .point_2_upload_image .image-uploader-thumb img {
  width: 30%;
}

.select_grid_form .point_2_upload_image .image-uploader-thumb .text {
  font-size: 10px;
  color: #8b8b8b;
}

