#text-writer__box {
  padding: 2rem 0;
  padding-bottom: 5rem;
  position: relative;
  z-index: 1;
}

#text-writer__box .box__input {
  border: none;
  border-bottom: 2px solid #a5a1a1;
  border-radius: 0px;
  font-weight: 600;
  color: #8b8b8b;
}

#text-writer__box .box__input:-ms-input-placeholder {
  color: #a5a1a1;
}

#text-writer__box .box__input::placeholder {
  color: #a5a1a1;
}

#text-writer__box .cancel-icon {
  position: absolute;
  right: 0px;
  top: 20%;
  background: #a5a1a1;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 999px;
  cursor: pointer;
}

#text-writer__box .cancel-icon:hover {
  background: #ff9999;
}

#text-writer__box .cancel-icon .iconify {
  font-size: 22px;
}

.theme-btn {
  border: none;
  color: white;
  background: #007880;
  padding: 5px 10px;
}

.theme-btn:hover {
  background: #006067;
  color: white;
}

.theme-color {
  color: #007880;
}

#create-page-dashboard {
  display: flex;
  flex-wrap: wrap;
  overflow-x: hidden;
  margin-bottom: 2rem;
}

#create-page-dashboard .sidebar {
  width: 160px;
}

#create-page-dashboard .content {
  width: calc(100% - 160px);
  padding: 20px;
  overflow: hidden;
}

#create-page-dashboard .content .content__container {
  width: 100%;
  padding: 20px;
  background: white;
  min-height: calc(100vh - 110px);
  box-shadow: 6px 6px 9px rgba(0, 0, 0, 0.08);
  border: 1px solid #e8e7e7;
  display: flex;
}

#create-page-dashboard .content .content__container .project-wrapper {
  height: auto;
  width: 80%;
}

#create-page-dashboard .content .content__container .project-wrapper .image-uploader-area {
  width: 100%;
  text-align: center;
  border: 1px dashed #8b8b8b;
  border-radius: 10px;
  min-height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}

#create-page-dashboard .content .content__container .project-wrapper .image-uploader-area img {
  max-width: 200px;
}

#create-page-dashboard .content .content__container .button_wrapper {
  width: 18%;
  margin-left: 2%;
}

#photoPholio-page .tow__layout_site {
  margin-top: 20px;
}

#photoPholio-page .tow__layout_site:first-child {
  margin-top: 0px;
}

#photoPholio-page .tow__layout_site .image-uploader-area {
  height: 250px;
}

#photoPholio-page .tow__layout_site .image-uploader-area img {
  width: 100px;
  margin-bottom: 10px;
}

#photoPholio-page .one__layout__site {
  height: 300px;
  margin-bottom: 30px;
}

#photoPholio-page .one__layout__site.default {
  min-height: 70vh !important;
  margin-bottom: 0px !important;
}

#photoPholio-page .page-text {
  color: #8b8b8b;
  font-weight: 600;
}
